.sidebar {
  position: absolute;
  top: 0;
  left: -300px;
  z-index: 9;
  display: flex;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.8);
  width: 300px;
  padding: 10px;
  height: 100vh;
  transition: 0.2s ease-in;

  row-gap: 20px;
  align-items: center;
}

.sidebar.open {
  position: absolute;
  top: 0;
  left: 0;
}

.background {

}

.nav {
  display: flex;
  flex-direction: column;
  font-size: 24px;

}

.nav a {
  color: white;
  text-decoration: none;
  padding: 20px 40px;
  border-radius: 10px;
  transition: 0.1s ease-in;
}

.nav a:hover {
  transform: scale(105%);
}

.nav a.active {
  font-size: 26px;
  color: #d52367;
}

.close {
  align-self: flex-end;
  width: 30px;
  height: 30px;
  background-color: rgba(255, 255, 255, 0.2);
  outline: none;
  cursor: pointer;
  border: none;
  transition: 0.1s ease-in;
}

.close:hover {
  background-color: rgba(255, 255, 255, 0.3);
}

